{
  position:absolute;
  top:50%;
  left:50%;
  -webkit-transform:translate(-50%,-50%);
          transform:translate(-50%,-50%);
  width:150px;
  height:150px;
  background:black;
  border:3px solid #3c3c3c;
  border-radius:50%;
  text-align:center;
  line-height:150px;
  font-family:sans-serif;
  font-size:20px;
  color:rgb(20, 241, 149);
  letter-spacing:4px;
  text-transform:uppercase;
  text-shadow:0 0 10px rgb(20, 241, 149);
  box-shadow:0 0 20px rgba(0,0,0,.5);
}
.ring:before
{
  content:'';
  position:absolute;
  top:-3px;
  left:-3px;
  width:100%;
  height:100%;
  border:3px solid transparent;
  border-top:3px solid rgb(20, 241, 149);
  border-right:3px solid rgb(20, 241, 149);
  border-radius:50%;
  -webkit-animation:animateC 2s linear infinite;
          animation:animateC 2s linear infinite;
}
.ringspan
{
  display:block;
  position:absolute;
  top:calc(50% - 2px);
  left:50%;
  width:50%;
  height:4px;
  background:transparent;
  -webkit-transform-origin:left;
          transform-origin:left;
  -webkit-animation:animate 2s linear infinite;
          animation:animate 2s linear infinite;
}
.ringspan:before
{
  content:'';
  position:absolute;
  width:16px;
  height:16px;
  border-radius:50%;
  background:rgb(20, 241, 149);
  top:-6px;
  right:-8px;
  box-shadow:0 0 20px rgb(20, 241, 149);
}
@-webkit-keyframes animateC
{
  0%
  {
    -webkit-transform:rotate(0deg);
            transform:rotate(0deg);
  }
  100%
  {
    -webkit-transform:rotate(360deg);
            transform:rotate(360deg);
  }
}
@keyframes animateC
{
  0%
  {
    -webkit-transform:rotate(0deg);
            transform:rotate(0deg);
  }
  100%
  {
    -webkit-transform:rotate(360deg);
            transform:rotate(360deg);
  }
}
@-webkit-keyframes animate
{
  0%
  {
    -webkit-transform:rotate(45deg);
            transform:rotate(45deg);
  }
  100%
  {
    -webkit-transform:rotate(405deg);
            transform:rotate(405deg);
  }
}
@keyframes animate
{
  0%
  {
    -webkit-transform:rotate(45deg);
            transform:rotate(45deg);
  }
  100%
  {
    -webkit-transform:rotate(405deg);
            transform:rotate(405deg);
  }
}

